import * as React from "react"
import { useStaticQuery, graphql, Link } from "gatsby"

import Layout from "../../components/layout"
import Seo from "../../components/seo"
import * as styles from "./styles/index.module.css"
import Img from "gatsby-image"
import BackgroundImage from "gatsby-background-image"

import Card from "@material-ui/core/Card"
import { Typography } from "@material-ui/core"

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    {
      allMarkdownRemark(sort: { fields: frontmatter___date, order: DESC }) {
        nodes {
          frontmatter {
            slug
            stack
            title
            date(formatString: "YYYY-MM-DD")
            thumb {
              childImageSharp {
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
          id
          excerpt(pruneLength: 100)
        }
      }
    }
  `)
  console.log(data)
  // const outter_links = data.site.siteMetadata.outter_links
  return (
    <Layout>
      <Seo title="Blogs" />
      <h1>Blog Posts</h1>
      <div className={styles.blog_item_card_container}>
        {data.allMarkdownRemark.nodes.map(p => {
          return (
            <div key={p.id} className={styles.blog_item_card}>
              <BackgroundImage
                style={{
                  height: "100%",
                  width: "100%",
                }}
                fluid={p.frontmatter.thumb.childImageSharp.fluid}
              >
                <Link
                  to={`/blogs/${p.frontmatter.slug}`}
                  style={{ textDecorationLine: "none" }}
                >
                  <Card style={{ backgroundColor: "rgba(255,255,255,.8)", padding: 8 }}>
                    <Typography variant="h5">{p.frontmatter.title}</Typography>

                    {/* <Img fluid={p.frontmatter.thumb.childImageSharp.fluid} /> */}
                    <p>{p.excerpt}</p>
                    <div style={{textAlign: "right"}} >{p.frontmatter.date}</div>
                    
                  </Card>
                </Link>
              </BackgroundImage>
            </div>
          )
        })}
      </div>
    </Layout>
  )
}

export default IndexPage
